ul{
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 35px;
    font-weight: 300;
    color: white;
    width: 60%;

    li{
        margin-bottom: 25px;
        a{
            font-size: inherit;
            color: inherit;
            text-decoration: none;
        }

        &:hover{
            font-weight: 500;
        }
    }
}